import "./brand.scss"
import nokia from "../../images/gadgets/nokiya.png"
import apple from "../../images/gadgets/apple.png"
import huawei from "../../images/gadgets/huawei.png"
import lg from "../../images/gadgets/lg.png"
import mi from "../../images/gadgets/mi.png"
import samsung from "../../images/gadgets/samsung.png"


export default function BrandLogo() {
    return(
        <div className="brand__logo">
            <div className="brand__logo__item">
                <img src={nokia} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={apple} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={samsung} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={samsung} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={samsung} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={samsung} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={huawei} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={lg} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={mi} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={mi} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={mi} alt="img" />
            </div>
            <div className="brand__logo__item">
                <img src={mi} alt="img" />
            </div>
        </div>
    )
}